@import './variables'

    
.home
    position: relative
    height: calc( 100% - $headerHeight )
    width: calc($fiveBlocks * 4)

    @media (min-width: 641px) and (max-width: 1200px)
        width: calc($fourBlocks * 3)
    
    @media (max-width: 640px)
        width: 100%
        padding-top: 17.5rem

    &__title
        margin: 0
        letter-spacing: -4.8px
        font-size: 9.231rem
        line-height: 1
        background: -webkit-linear-gradient(top left,#b8b8b8,#fff 60%)
        -webkit-background-clip: text
        -webkit-text-fill-color: transparent

        @media (max-width: 1200px)
            font-size: 7.692rem

        @media (max-width: 640px)
            font-size: 5.385rem
            letter-spacing: -1.8px


        &::after
            content: ""
            display: inline-block
            width: 18px
            height: 18px
            margin-left: 20px
            border-radius: 100%
            background: #fff

            @media (max-width: 640px)
                width: 7px
                height: 7px
                margin-left: 8px

    .news
        position: absolute
        width: 25%
        padding: 0 $basePagePadding
        height: 200px
        right: 0
        bottom: $basePagePadding

        @media ( max-width: 640px )
            display: none

        @media (min-width: 641px) and (max-width: 1200px)
            width: 33.3%

        &__date
            margin-bottom: 24px
            opacity: .2

        &__link
            font-size: $mediumFontSize
            color: #ffffff
            font-weight: 300